import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import Item from '../components/Item'
import RelateBox from '../components/RelateBox'

Vue.use(Router)

export default new Router({
    mode:'history',
    routes: [
        {
            path: '/',
            name: 'Home',
            component: Home
        }, 
        {
            path: '/item/:id',
            name: 'Item',
            component: Item
        }, 
        {
            path: '/relate',
            name: 'RelateBox',
            component: RelateBox
        }, 

    ]
})
