<script>
import RelateBox from "./RelateBox.vue";
import axios from "axios";
export default {
  name: "Item",
  props: {
    msg: String,
  },
  components: {
    RelateBox,
  },
  data() {
    return {
      product: "",
      facebook_link: "",
    };
  },
  methods: {},
  watch: {
    "$route.params.id": {
      handler: function (id) {
        let fullPath = this.$route.fullPath;
        let baseUrl = window.location.origin;
        let routerItem = `${baseUrl}${fullPath}`;
        axios
          .get(`/api/blog/${id}`)
          .then((res) => (this.product = res.data.data))
          .catch((error) => (this.error = error));
        this.facebook_link = `https://www.facebook.com/sharer/sharer.php?u=${routerItem}`;
      },
      deep: true,
      immediate: true,
    },
    // "$route.params.id": {
