import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)
// const url ='api/'

export default new Vuex.Store({
    state:{
       blogs:'',
    
    },
    getters:{
        getMonAu(state){
            return state.blogs.slice(0,3);
        },
        getMonViet(state){
            return state.blogs.slice(3,6);
        },
        getDeXuat(state){
            return state.blogs.slice(0,7)
        }
    },
    actions:{
        getBlog({commit}){
            axios.get('/api/blog/getBlog')
            .then(res =>commit('GETBLOG',res.data))
        },
       
    },
    mutations:{
        GETBLOG(state,data){
            state.blogs = data.data
        },
        

    }
})
